.dft-badge {
	&--nav {
		@include govuk-font(14, regular);
		
		min-width: 24px;
		text-align: center;
		border-radius: 3px;
		padding: 3px 4px 1px;
		display: inline-block;
		background-color: $dft-white;

		@include govuk-media-query($from: tablet) {
			min-width: 25px;
			padding: 5px 5px 0;
		}
	}

	&--heading {
		@include govuk-font(19, regular);
		display: inline-block;
		color: $govuk-secondary-text-colour;
	}
}
